.container {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
}

.input-cont {
  align-items: center;
  text-align: center;
  margin-bottom: 5vh;
}

/* Scroll Bar */
::-webkit-scrollbar-track
{
-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
border-radius: 10px;
background-color: #F5F5F5;
}

::-webkit-scrollbar
{
width: 12px;
background-color: #F5F5F5;
}

::-webkit-scrollbar-thumb
{
border-radius: 10px;
-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
background-color: #555;
}